// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import toast from 'react-hot-toast'
import { useDropzone } from 'react-dropzone'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import {
  Card,
  Input,
  Button,
  CardBody,
  Label,
  ListGroup,
  ListGroupItem,
  Col,
  Row,
  Progress
} from 'reactstrap'

// ** Third Party Components
import 'cleave.js/dist/addons/cleave-phone.us'
import { X, Paperclip, File } from 'react-feather'
import Avatar from '@src/@core/components/avatar'
import { getData, addNotes, deleteNotes, updateNotes } from './store/index'
import { useDispatch, useSelector } from 'react-redux'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'
import { useParams } from 'react-router-dom'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

const activeOrgId = activeOrganizationid()
const userId = orgUserId()
const MySwal = withReactContent(Swal)
// ** Images
const Notes = props => {
  // ** Hook
  const { moduleRefId, tabId } = props

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const renderClient = row => {

    return (
      <Avatar
        initials
        className='me-1'
        color={'light-primary'}
        content={row.username.charAt(0) || 'T'}
      />
    )

  }

  const dispatch = useDispatch()
  //** State */
  const [files, setFiles] = useState([])
  const [data, setData] = useState([])
  const [filesArr, setFilesArr] = useState([])
  const [attachmentIds, setAttachmentsId] = useState([])
  const [comment, setComment] = useState('')
  const [editcomment, setEditComment] = useState('')
  const [fileUploadProgress, setProgress] = useState(0)
  const { id } = useParams()
  const store = useSelector(state => state.notes)

  const deletefn = (row) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteNotes(row))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Invoice has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const editNote = (k) => {
    const temp = data.map((obj, i) => {
      if (i === k) {
        setEditComment(obj.description)
        return { ...obj, editFlag: true }
      } else {
        return { ...obj, editFlag: false }
      }
    })
    setData(temp)
  }
  const dateformat = (val) => {

    return moment(val, 'x').format('YYYY, MMM-D h:m A')

  }

  const configUpload =
  {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: data => {
      //Set the progress value to show the progress bar
      setProgress(Math.round((100 * data.loaded) / data.total))
    }
  }

  const uploadFiles = () => {

    const formData = new FormData()
    files.forEach((file, key) => {
      formData.append(`files[${key}]`, file)
    })
    formData.append('id', moduleRefId)
    formData.append('organizationId', activeOrgId)
    formData.append('createdBy', userId)
    setBlock(true)
    axios.post('/google/clientfolders', formData, configUpload).then((res) => {
      setAttachmentsId([...attachmentIds, ...res.data.attachment_ids])
      setFiles([])
      setBlock(false)
    }).catch((err) => { console.log(err); setBlock(false) })
  }

  const { getRootProps, getInputProps } = useDropzone({
    multiple: true,
    onDrop: (acceptedFiles, rejectedFiles) => {
      if (rejectedFiles.length) {
        toast.error('You can only upload image Files!.')
      } else {
        setFiles(acceptedFiles)
        setFilesArr([...filesArr, ...acceptedFiles])
      }
    }
  })

  const renderFileSize = size => {
    if (Math.round(size / 100) / 10 > 1000) {
      return `${(Math.round(size / 100) / 10000).toFixed(1)} mb`
    } else {
      return `${(Math.round(size / 100) / 10).toFixed(1)} kb`
    }
  }

  useEffect(() => {
    if (files.length > 0) {
      uploadFiles()
    }
  }, [files])

  const handleRemoveFile = file => {
    const uploadedFiles = files
    const filtered = uploadedFiles.filter(i => i.name !== file.name)
    setFiles([...filtered])
    setProgress(0)
  }

  const saveNote = async (obj) => {
    obj.description = editcomment
    if (obj.description !== '' || attachmentIds.length > 0) {
      await dispatch(updateNotes(obj))
      toast.success('Comment Updated Successfully.', { position: 'bottom-right' })
    }
  }

  useEffect(async () => {
    if (tabId === 'clientnotes' || tabId === 'tasknotes' || tabId === 'dscnotes' || tabId === 'servicetemplate') {
      await dispatch(getData(id))
    }
  }, [tabId])

  useEffect(() => {
    setData(store.data || [])
  }, [store.data])

  const postComment = async () => {
    if (comment !== '' || attachmentIds.length > 0) {
      const data = {
        organizationId: activeOrgId,
        createdBy: userId,
        updatedBy: userId,
        description: comment,
        attachmentIds,
        moduleReferenceId: moduleRefId,
        moduleId: id
      }
      setBlock(true)
      await dispatch(addNotes(data))
      toast.success('Comment Added Successfully.', { position: 'bottom-right' })
      setComment('')
      setFiles([])
      setFilesArr([])
      setProgress(0)
      setBlock(false)
    }

  }

  const fileList = files.map((file, index) => (
    <ListGroupItem key={`${file.name}-${index}`} className='d-flex align-items-center justify-content-between'>
      <div className='file-details d-flex align-items-center'>
        <div className='file-preview me-1'>
          <Avatar />
        </div>
        <div>
          <p className='file-name mb-0'>{file.name}</p>
          <p className='file-size mb-0'>{renderFileSize(file.size)}</p>
        </div>
      </div>
      <Button color='danger' outline size='sm' className='btn-icon' onClick={() => handleRemoveFile(file)}>
        <X size={14} />
      </Button>
    </ListGroupItem>
  ))

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <CardBody>

            <fieldset className='form-label-group mb-50'>
              <Label className='form-check-label' for={`add-comment-`}>
                Add Comment
              </Label>
              <Input id={`add-comment-`} type='textarea' rows='3' placeholder='Add Comment' value={comment} onInput={(e) => setComment(e.target.value)} />
            </fieldset>

            <input {...getInputProps()} />

            <Button type='button' color='primary' size='sm' {...getRootProps({ className: 'dropzone' })} >
              <Paperclip size={14} className=''></Paperclip>
            </Button>

            <Button color='primary' className='ms-1' size='sm' onClick={() => postComment()}>
              Post Comment
            </Button>

            {fileUploadProgress > 0 && <Progress color={fileUploadProgress < 100 ? 'primary' : 'success'} value={fileUploadProgress} />}
            <ListGroup>{fileList}</ListGroup>

            {data.map((item, k) => {
              return (
                <div className='d-flex align-items-start my-1' key={k}>
                  {renderClient(item)}
                  <div className='profile-user-info w-100'>
                    <div className='d-flex align-items-center justify-content-between'>
                      <h6 className='mb-0'>{item.username}</h6>
                    </div>
                    {!item.editFlag &&
                      <Row className='mt-1'>
                        <div className='white-space-wrap'>{item.description.replace(/(\\n)/g, "\n")}
                          <Row>
                            {
                              item.files[0] !== null &&
                              item.files.map((file, k) => {
                                return (
                                  <Col md={'4'} key={k} className='file-details d-flex align-items-center cursor-pointer'>
                                    <ListGroupItem className='col-12 d-flex mb-1 align-items-center'>
                                      <div className='file-preview me-1'>
                                        <img width={25} height={25} src={`https://drive.google.com/thumbnail?id=${file.driveid}`} alt="image" />
                                      </div>
                                      <a target='_blank' className='file-name mb- text-overflow-ellipsis' href={`http://docs.google.com/fileview?id=${file.driveid}&hl=en&pid=explorer&efh=false&a=v&chrome=false&embedded=true`}>{file.originalfilename.split('.')[0].length > 15 ? `${file.originalfilename.split('.')[0].substr(0, 15)}.${file.extension}` : file.originalfilename}</a>
                                    </ListGroupItem>
                                  </Col>
                                )
                              })
                            }
                          </Row>
                        </div>
                        <div className='d-flex mt-1'>
                          <span className='text-sm'>{dateformat(item.createdon)}</span>
                          &nbsp;&nbsp;
                          {!item.editFlag &&
                            <Col className='d-flex' md={1}>
                              <span className='text-primary cursor-pointer' onClick={() => editNote(k)} >.Edit</span> &nbsp;&nbsp;
                              <span className='text-danger cursor-pointer' onClick={() => deletefn(item)} >.Delete</span>
                            </Col>
                          }
                        </div>
                      </Row>
                    }
                    {item.editFlag &&
                      <Row className='mt-2'>
                        <Input type='textarea' roes='3' value={editcomment.replace(/(\\n)/g, "\n")} onInput={(e) => { setEditComment(e.target.value) }} />

                        <Col className='d-flex justify-content-end mt-1' >

                          <Button size='sm' color='warning' outline onClick={() => editNote(-1)}>Cancel</Button>
                          <Button size='sm' color='primary' className='ms-1' onClick={() => saveNote(item)} >Save</Button>
                        </Col>
                      </Row>
                    }
                  </div>
                </div>
              )
            })
            }
          </CardBody>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default Notes
