import { useEffect, useState, Fragment } from 'react'

// ** Reactstrap Imports
import {
  Badge,
  Card,
  CardBody,
  CardTitle,
  Col,
  Row
} from 'reactstrap'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import axios from '@src/configs/axios/axiosConfig'
import { useParams } from 'react-router-dom'
import { Plus, X } from 'react-feather'

const Compliances = ({ tabId }) => {

  const { id } = useParams({})
  const [clientCompliances, setClientCompliances] = useState([])
  const [compliances, setCompliances] = useState([])

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const getCompliances = () => {
    setBlock(true)
    axios.post('/compliances/list_min', {})
      .then((res) => { setCompliances(res.data.compliances); setBlock(false) })
      .catch((err) => { console.log(err); setBlock(false) })
  }

  const getClientCompliance = () => {
    setBlock(true)
    axios.post('/clients/compliancelist', { contactid: id })
      .then((res) => { setClientCompliances(res.data.compliances[0].compliancestags || []); setBlock(false) })
      .catch((err) => { console.log(err); setBlock(false) })
  }

  useEffect(() => {
    if (tabId === 'compliance') {
      getCompliances()
      getClientCompliance()
    }
  }, [tabId])

  const addCompliance = (compliance) => {
    setBlock(true)
    axios.post('/clients/addcompliance', { id: compliance.id, clientId: id })
      .then(() => { getClientCompliance(); setBlock(false) })
      .catch((err) => { console.log(err); setBlock(false) })
  }

  const removeCompliance = (compliance) => {
    setBlock(true)  
    axios.post('/clients/deletecompliance', { id: compliance.id, clientId: id })
      .then(() => { getClientCompliance(); setBlock(false) })
      .catch((err) => { console.log(err); setBlock(false) })
  }
  
  const renderCompliance = (compliance, k) => {
    if (clientCompliances.indexOf(compliance.id) === -1) {
      return (
        <Col key={k} className='my-50'>
          <Badge color='primary' onClick={() => addCompliance(compliance)} className='cursor-pointer'>
            {compliance.name}
            <Plus></Plus>
          </Badge>
        </Col>
      )
    }
  }

  const renderClientCompliance = (compliance, k) => {
    if (clientCompliances.indexOf(compliance.id) > -1) {
      return (
        <Col key={k} className='my-50'>
          <Badge color='success' onClick={() => removeCompliance(compliance)} className='cursor-pointer'>
            {compliance.name}
            <X />
          </Badge>
        </Col>
      )
    }
  }

  return (
    <Card>
      <UILoader blocking={block} loader={<Loader />}>
        <CardBody >
          <CardTitle className='align-items-end card-title d-flex justify-content-between mb-0' tag='h5'>
            Compliances
          </CardTitle>
          <Row className='mt-2'>
            {
              compliances.map((compliance, k) => {
                {
                  return renderCompliance(compliance, k)
                }
              })
            }
          </Row>
          <hr className='my-2'></hr>
          <Row>
            {
              compliances.map((compliance, k) => {
                {
                  return renderClientCompliance(compliance, k)
                }
              })
            }
          </Row>

        </CardBody>
      </UILoader>
    </Card>
  )
}

export default Compliances
